var staticRenderFns = [function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"project",attrs:{"id":"minor-presence"}},[_c('h3',[_vm._v("Introduction")]),_c('p',[_vm._v(" There are two ways of reading institutions. The first way is the “image” of the institution. The image is the official way an institution wishes to be read by the public. The second way of reading an institution is through its “presence.”"),_c('sup',[_vm._v("1")]),_vm._v(" Presence is the reciprocal relationship and shaping between the institution and those interacting with it. The latter relationship of presence is often unacknowledged because it troubles the logical reading of an institution's image. ")]),_c('p',[_vm._v(" The library as an institution is par excellence an archival space dedicated to the official telling of history and distribution of information. ")]),_c('p',[_vm._v(" The first library in the United States intended to serve undergraduates is Harvard’s Lamont Library."),_c('sup',[_vm._v("2")]),_vm._v(" By using it as a case study, this project seeks to queer the official role of the library by studying its presence rather than its image. This shift in legibility allows for a more pluralistic understanding of history and identity that is broader in its narrative. ")]),_c('p',[_vm._v(" This initial part of our project is the first trip into a series of questions we are engaging with about the institutional library as a space. It is divided into 5 reflections. In reflection 1, we discussed our use of a queer methodology through the frame of the minor. Reflections 2 and 3 gave a brief historical account of the library as an institutional space and looked into Harvard’s Lamont library as a case study. Reflection 4 helps us to pin down a few key definitions. Through the framework of the minor, we introduce the distinction between the image of a space and its presence. To conclude we reflect on the potentialities that come from remixing, reordering, and redistributing the popular."),_c('sup',[_vm._v("3")])]),_c('p',[_vm._v(" We have been writing these reflections asynchronously, and as we write along, they are increasingly in conversation with each other. Weaving together these 5 pairs of loosely defined reflections, we hope to present our intention towards remixing knowledge in different contexts, and producing a “messy” research project that hesitates to be fully linear in direction. ")]),_c('div',{staticClass:"footnote"},[_c('hr'),_c('p',[_vm._v(" 1 Quoting from the Editor’s introduction for Radical History Review Vol 2014 Issue 120: “Queerness and the archival are structured by their own distinct habitual wranglings with absence and presence.” ")]),_c('p',[_vm._v(" 2 Harvard Property Information Center: "),_c('a',{attrs:{"target":"_blank","href":"https://harvardplanning.emuseum.com/sites/587/lamont-library"}},[_vm._v("link")])]),_c('p',[_vm._v(" 3 We are still defining this term for ourselves. At the moment it means the things that are chosen as a representation of how things are. ")])]),_c('h3',[_vm._v("Reflection 1: "),_c('i',[_vm._v("The Minor as a Method")]),_vm._v(" "),_c('sup',[_vm._v("4")])]),_c('img',{staticClass:"center",attrs:{"src":"/images/minor_presence_1.png"}}),_c('p',{staticClass:"head"},[_vm._v(" Minor Presence - Tejumola Bayowa ")]),_c('p',[_vm._v("When one talks about a minor ideology, one speaks from a position outside the normative frame. Thus to speak from the position of the minor is to challenge the “epistemological assumptions and ontological conditions that uphold the order of things.” (20) At its root a minor approach is a reordering of the structures that constrain the world as we know it. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Interesting to think of the \"minor\" in the ideological instance; I feel that usually, ideology is complicit with /framed together with the \"major,\" the grand narrative/normative narrative that validates, normalizes, and naturalizes itself through its corresponding ideology. (the strong, legible image) ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Exactly like remixing! Re-ordering of existing materials with different structural principles? ")]),_c('p',[_vm._v(" With this in mind, it becomes clearer what a minor methodology has in relation to queerness. Queer, in the general sense is defined as “differing in some way from what is usual or normal.” Queer is also understood in more sexual terms; “relating to, or characterized by sexual or romantic attraction to members of one’s own sex.” (merriam webster dictionary) Minor theory, like queerness is dependent on ones positionality. A position that is located outside conventional behavior. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Thinking with Hentyle's framework, such an understanding of queerness also comes from the major's perspective (understanding) of the \"other?\" (and its internalization, homonormativity) ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Interesting how the dictionary (the authoritative archive) defines queerness only in terms of same-sex relationships. ")]),_c('p',[_vm._v(" Similar to minor theory, utilizing queer theory as a means of understanding the structures that define the world challenges heteronormative logics that suppress and limit already existing potentialities of subaltern futures. ")]),_c('p',[_vm._v(" In our use of the term minor, adjacent to it is the term presence. Both words are friends stitched together to formulate the argument of this research paper. Presence for us is the real. It is not a simulacrum or a representation. It is in short, things as they are. Both minor and presence are connected because to speak from the perspective of both is to speak from the real and not an image. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Cute image! ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: But this realness is often implicated by the image; not a one-way street. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: yes, i agree. which is why i think it is the real. it recognizes its complicity, its errors, its \"mistakes\" as you used it in reference to spivak in your reflection. image is a one way street. \"real\" is a junction. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: I love this - Image is a one-way street. \"Real\" is a junction ")]),_c('p',[_vm._v(" Therefore, when we use the term minor presence what we are referring to is a voice that speaks from the real with an intention of reordering the formation of our global system by dismantling hierarchical definitions of knowledge that exclude alternative ways of being. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Again, for us, this \"real\" does not have a notion of purity or isolation from the image. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: real is the opposite of purity. it is dirty, messy, far from linear. maybe more needs to be said about real in relation to image and presence. or maybe a different word. or not brought up. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Yes. I agree with you that more need to be said - I guess we need to give \"real\" more context as its interpretation can change so much depending on context. ")]),_c('p',{staticClass:"head"},[_vm._v(" My Silly Mistake - Mingjia Chen ")]),_c('p',[_vm._v(" When the word “minor” first makes an appearance in TJ and my notes, I immediately think about the geopolitical construct, Asia Minor, which is loosely bounded by the modern national borders of Turkey. Until a few minutes ago, I was unaware of the (quite common sense) information that I had just typed down. For me, the term was not particularly familiar-sounding nor interesting, and I naturally made the ignorant mistake of presumption everytime it flashes by in my mind. Recently, I have been reading Hentyle Yapp’s latest book, Minor China. Asia, minor. In a naively instinctive attempt to make connections, I made the following mistake: I thought the term was describing the Asian diaspora community at large. ")]),_c('p',[_vm._v(" Asia, minor. The etymological origins of these words remain unquestioned. Let us dwell momentarily on the term Asia. A quick google plus Wikipedia search helps to override my unsolicited attempt to relate its historicity and contemporary orientalism (and also proves my gullibility). Part of the Roman Empire, Asia used to be the name of the province that sits on the North shores of the Mediterranean Sea. Later, the bounds of the term is expanded to include the whole continent east of the Roman Empire, therefore making the peninsula province the “Asia Minor.” ")]),_c('p',[_vm._v(" Returning to the term minor, and operating for a second within a seemingly unrelated context - for the purpose of framing our research, looking at the term’s etymology is less productive. Rather, we are interested in a particular theorization of the “minor” as a method supplied by Hentyle Yapp. In the field of art history, according to Yapp, the minor method “relies upon the formal and more seemingly minor details of artworks to illustrate how context and history for understanding the transnational have been privileged over an examination of its becoming.”"),_c('sup',[_vm._v("5")]),_vm._v(" In short, by examining the structural instead of the representational existence of the minor, one is able to read the becoming of the artwork, of how its major (nationalistic) image comes to be. The minor begs one to sense it differently, and for us, methodologically."),_c('sup',[_vm._v("6")])]),_c('p',{staticClass:"tb"},[_vm._v(" TB: key point ")]),_c('p',[_vm._v(" Technically, the interlude above has nothing to do with the rest of our writings. It only speaks to the elite institutional library (our scope; a pressure point for us to enter the discussion of institutional image, and its realness, of what we call presence) ever so distantly. It derails, neglects and perhaps even rejects the modus operandi of the library: Afterall, my comprehension of the Asia Minor takes a pause at the mere surface of the Wikipedia page. ")]),_c('p',[_vm._v(" Nonetheless, one thread is traceable. It weaves between our initial research and the above triviality. In both cases, the curious yet hesitant inquiry of the image for a certain abstract place is initiated by making a founding error of generalization. In our early discussions about the “un-intended uses” of the institutional library and its community presence as a social space, we assumed the library to be a homogenous space that considers anything other than the access of preserved knowledge as “non-normative.” We were ignorant about and unaware of a rich and critical discourse of the library as a social space, and its position within the social safety net. "),_c('sup',[_vm._v("7")])]),_c('p',{staticClass:"tb"},[_vm._v(" TB: we were. ")]),_c('p',[_vm._v(" Therefore, it is this founding mistake that we must first acknowledge and expand on. It is also this mistake that drives our sustained interest, and produces a self-critical research method that seeks to remixing rather than producing knowledge; one which might inhabit the name queer. We contend that through understanding the minor as a method, one could understand the institutional library as a plural form, and from the bottom up - its official image cuts in and out such plurality. More to be elaborated, but I conclude this interlude (now a prelude) with a reminder that “we must know what mistake to make [...] and also must know how to defend our mistake as the one that will allow us to live.” "),_c('sup',[_vm._v("8")])]),_c('p',{staticClass:"tb"},[_vm._v(" TB: well put ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: live by and through mistake. nice quote ")]),_c('p'),_c('div',{staticClass:"footnote"},[_c('hr'),_c('p',[_vm._v(" 4 Yapp, Minor China, 1 - 36. ")]),_c('p',[_vm._v(" 5 Yapp, Minor China, 23. ")]),_c('p',[_vm._v(" 6 Yapp, Minor China, 6. ")]),_c('p',[_vm._v(" 7 From an anonymous source. ")]),_c('p',[_vm._v(" 8 Spivak, An Aesthetic Education in the Era of Globalization, 28. ")])]),_c('h3',[_vm._v("Reflection 2: "),_c('i',[_vm._v("The Institutional Library as a Space")])]),_c('img',{staticClass:"center",attrs:{"src":"/images/minor_presence_2.png"}}),_c('p',{staticClass:"head"},[_vm._v(" Library as a Space - Tejumola Bayowa ")]),_c('p',[_vm._v(" The Lamont Library is named after Thomas Lamont, a Harvard graduate and New York based banker. It was designed by Coolidge Shepley Bulfinch and Abbott and completed in 1949 after two years of construction. "),_c('sup',[_vm._v("9")]),_vm._v(" Lamont was built to address both the limited stack space of Widener Library ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Widener Library was largely intended for graduate students historically. ")]),_c('p',[_vm._v(" and specifically to serve male undergraduates. Female Students from Radcliffe University, the sister college of Harvard at the time, would not have access to the library until 1967. "),_c('sup',[_vm._v("10")])]),_c('p',[_vm._v(" Today, Lamont library is described as a 24 hour space supporting the humanities and social sciences.” "),_c('sup',[_vm._v("11")]),_vm._v(" The spaces provided by the library include: special collections reading rooms called the Woodberry Poetry Room, computer and media devices in the Media lab, a quiet space for multimedia projects in the Media lab annex, quiet study rooms called Farnsworth room, and group work and seating area called the Donatelli reading room. ")]),_c('p',[_vm._v(" Being that our research is concerned with the Lamont Library at Harvard University it is worth touching on the library as an institutional space. Firstly, as an undergraduate student library, Lamont is distinct in its role from public libraries. Firstly, a school library like Lamont serves a specific role within an institution that provides resources mainly to students and affiliates of the associated institution. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: And seems to be always central to the institution and its image ")]),_c('p',[_vm._v(" Resources provided include but are not limited to books, learning spaces for independent work, access to computers and the internet, and archival access and materials ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Saw an interesting comment that \"the public library is one of the only supplier who provide access to technology for free\" ")]),_c('p',[_vm._v(" These resources are essential to providing a more equitable environment in university campuses. Another distinction between the public library and an institutional library is that institutional libraries serve to enhance a school's "),_c('b',[_vm._v("image")]),_vm._v("."),_c('sup',[_vm._v("12")])]),_c('p',{staticClass:"mc"},[_vm._v(" MC: I feel that this is a central distinction ")]),_c('p',[_vm._v(" Institutions advertise their libraries as attractions to the public and tools of validating their prestigiousness through the size of their holdings. These resources are as much political in their intentions as they are to serve its users. ")]),_c('p',{staticClass:"head"},[_vm._v(" Library as a Space - Mingjia Chen ")]),_c('p',[_vm._v(" Thinking about the library as a space does not necessarily imply that one is reading the material qualities of the library as a phenomenological space. The Library as concerned by our research has so far been investigated as a geographical and policy-informed space rather than an architectural one."),_c('sup',[_vm._v("13")]),_vm._v("For instance, in the province of British Columbia, the public library system as a social space seems to occupy an increasingly marginal position within the complex structure of governmental programs both politically and financially. Yet, in the midst of the increased digitization of public services and the privatization of information, the public libraries in BC have become the forefront of maintaining social security - "),_c('b',[_vm._v("it has become the safety net of the social safety net.")]),_c('sup',[_vm._v("14")])]),_c('p',{staticClass:"tb"},[_vm._v(" TB: nicely said ")]),_c('p',[_vm._v(" The municipal library, despite its primary function as a shared public resource for knowledge, has been crucial for people struggling to find their place in the city. According to Freeman, many municipal libraries across the Vancouver region have become the only place where people could access technologies for free, and to actively receive personal support for filling out governmental assistance applications. However, the librarians’ willingness to help has put the public library in a double bind."),_c('sup',[_vm._v("15")]),_vm._v(" Neither adequately supported by government funding nor part of their mandate, the library staff members constantly receive complaints “from housed patrons while trying to support their homeless patrons to find safe shelter during the day, sleep in safety, and enjoy the space of the library.”"),_c('sup',[_vm._v("16")]),_vm._v(" The presence of the public library branch in the community as a center for social support providing both infrastructure and assistance has disrupted its persistent image of a seemingly apolitical space for public education."),_c('sup',[_vm._v("17")])]),_c('p',[_vm._v(" The academic library, on the other hand, faces a different contradiction around its uses. Students who need a productive workspace would make the academic library their “crib.”"),_c('sup',[_vm._v("18")]),_vm._v(" Productivity does not only entail intensive periods of scholarly activities, it also includes the in-betweens of the working sessions, such as using the bathroom, eating, taking a nap, and scrolling their smartphones. Such uses have existed since the establishment of Harvard’s Lamont Library in 1949, the first American academic library dedicated to (male) undergraduate students."),_c('sup',[_vm._v("19")])]),_c('img',{attrs:{"src":"/images/minor_presence_1.jpg"}}),_c('p',{staticClass:"caption"},[_vm._v("UAV 605 (HC 326) Students reading in Farnsworth Room, ca. 1954. Source: Harvard University Archives.")]),_c('p',[_vm._v(" However, it was not until recently that the institutions started to rethink the library as a space beyond being a repository for archives. As the architect and library planning specialist Geoffery T. Freeman suggests, the academic library has been traditionally planned to prioritize the processing of archival resources and materials. However, as the institution shifts its paradigm from a teaching-based pedagogy into a learning-based one, such programming would no longer meet the students’ ever-evolving needs. With the increasing digitalization of archival materials, the students need a place where she “gets serious,” and one which “enables [her] to advance [her] learning experiences.”"),_c('sup',[_vm._v("20")]),_vm._v(" Further, by anticipating students’ social uses, Freeman contends that the newly designed academic library should reflect and enhance the vision (or what we could call an official image) of its hosting institution."),_c('sup',[_vm._v("21")])]),_c('p',{staticClass:"tb"},[_vm._v(" TB: the intention of the library as furthering the institutions image is a point i find interesting and discussed briefly as well. interested in finding more evidence for this claim. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: And interested in how successful this image-building has been ")]),_c('p',[_vm._v(" We see that the question of the library’s social uses (such as providing access to technology, sanitary infrastructures, and living spaces) which has been under critical discussion in the realm of public policy has been appropriated by the institution to build its new image as a space of diversity and inclusion. Does this mean that the academic library has become truly accessible? Some suggest that for students, especially freshmen, the poster-child library of an institution can still be very intimidating."),_c('sup',[_vm._v("22")]),_vm._v(" Students are haunted by the dominant and legible image that has been condensed from the eurocentric historical traditions of higher institutions, and the enlightenment. An academic deterrence. An almost forbidden site of secrecy. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: is the institutional library public/accessible? not in its image but in its presence it has been utilized as one. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: This is a good point to consider - I think that when thinking about presence we also need to look at what this means for those who are denied service. I remember that on crimson one student mentioned that the Lamont library is a good place to be away from the \"homeless\" on campus at 3 AM - what does this say about presence? ")]),_c('img',{staticClass:"center",attrs:{"src":"/images/minor_presence_2.jpeg"}}),_c('p',{staticClass:"caption"},[_vm._v("Image source: McDonald's.")]),_c('p',[_vm._v(" In fact, we feel that there is a sense of intimacy between the student and their library of choice. Such intimacy is not established through the permission and anticipation of uses established by the institution. Rather, it is accomplished precisely because the student finds this place between the image of solemness and the loosely regulated reality inhabitable. In other words, it is not the best choice, but it is worth the time for what it provides. It is what it is, like finding salad as a “healthy” option at a fried chicken joint."),_c('sup',[_vm._v("23")]),_vm._v(" Yet, something feels off when the institution cuts into this in-between space and consumes it as part of its ever growing official image - I keep thinking about the overpriced Mcdonald's “menu hacks” which took inspiration from people creatively interpreting their regular offerings."),_c('sup',[_vm._v("24")])]),_c('p',{staticClass:"tb"},[_vm._v(" TB: well put ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: instituition institutioning. ")]),_c('div',{staticClass:"footnote"},[_c('hr'),_c('p',[_vm._v(" 9 "),_c('a',{attrs:{"target":"_blank","href":"https://harvardplanning.emuseum.com/sites/587/lamont-library"}},[_vm._v("link")])]),_c('p',[_vm._v(" 10 Schwager, & Ulrich, Yards and Gates: Gender in Harvard and Radcliffe history. ")]),_c('p',[_vm._v(" 11 "),_c('a',{attrs:{"target":"_blank","href":"https://library.harvard.edu/libraries/lamont"}},[_vm._v("link")])]),_c('p',[_vm._v(" 12 Image is used here in the way we have defined it as the way an institution presents itself that does not fully represent its presence. ")]),_c('p',[_vm._v(" 13 The boundary between the geographical notion of space & place, and their architectural sisters remain ambiguous - the two sets of terms (sharing the same words) are often applied interchangeably between the discipline of geography and architecture. ")]),_c('p',[_vm._v(" 14 Lisa Freeman, “Public Libraries are Becoming the New Social Safety Net” (2017), "),_c('a',{attrs:{"target":"_blank","href":"https://www.policynote.ca/public-libraries-are-becoming-the-new-social-safety-net/"}},[_vm._v("link")])]),_c('p',[_vm._v(" 15 The double bind, coined by Gayatri Spivak in her book An Aesthetic Education in the Era of Globalization, describes the situation where one is trapped in a predicament of making a decision between two options, neither of which seems preferable. ")]),_c('p',[_vm._v(" 16 Lisa Freeman, “Public Libraries are Becoming the New Social Safety Net” (2017) ")]),_c('p',[_vm._v(" 17 The public library provides an array of different uses: ranging from sanitary infrastructures to meeting rooms for booking. Curiously, the ALA (American Library Association) lists the “literacy of Homeless” as the library’’s top social function. "),_c('a',{attrs:{"target":"_blank","href":"https://www.ala.org/tools/research/librariesmatter/category/social-role-library"}},[_vm._v("link")])]),_c('p',[_vm._v(" 18 "),_c('a',{attrs:{"target":"_blank","href":"https://www.thecrimson.com/article/2022/2/17/mtv-crib-lamont/"}},[_vm._v("link")])]),_c('p',[_vm._v(" 19 "),_c('a',{attrs:{"target":"_blank","href":"http://libraryhistory.pbworks.com/w/page/16964653/Lamont%20Library"}},[_vm._v("link")])]),_c('p',[_vm._v(" 20 Geoffery T. Freeman, AIA, “The Library as a Place: Changing in Learning Patterns, Collections, Technology and Use,” in Library as a Space: Rethinking Roles, Rethinking Place, 3. ")]),_c('p',[_vm._v(" 21 Geoffery Freeman, “The Library as a Place,” 7. ")]),_c('p',[_vm._v(" 22 From a warm and friendly anonymous source. ")]),_c('p',[_vm._v(" 23 If you ignore the sauces, of course. ")]),_c('p',[_vm._v(" 24 "),_c('a',{attrs:{"target":"_blank","href":"https://www.washingtonpost.com/food/2022/02/08/mcdonalds-menu-hacks-review/"}},[_vm._v("link")])])]),_c('h3',[_vm._v("Reflection 3: "),_c('i',[_vm._v("The (intimidating) image of Harvard Libraries")])]),_c('img',{staticClass:"center",attrs:{"src":"/images/minor_presence_3.png"}}),_c('p',{staticClass:"head"},[_vm._v("Institutional Fear and Criminality - Tejumola Bayowa ")]),_c('p',[_vm._v(" Walking into any space can be fearful. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: So true, especially sometimes when you sense that the space subtly rejects your presence. ")]),_c('p',[_vm._v(" Walking into an institutional space in particular brings certain fears. It was not particularly a surprise to hear our librarian friend speak about the fears of Harvard undergraduates using Widener library as a working space. The roots of these fears are varied and it would be impossible to assign one universal root. But while no particular reason can be selected, we can argue that the image of the institution is interlinked with these fears. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Like a shadow, or a specter ")]),_c('p',[_vm._v(" When thinking about institutions and my former fears of institutions, I question what the root of these fears could be. One reason could be the power which institutions have garnered for themselves over time. For example, the University has made an image of itself as an essential link to the betterment of society. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: And the academic library is considered to be central for that betterment, as Jeffery Freeman discussed. ")]),_c('p',[_vm._v(" Over time, the general route to a better life has been reduced to one route; go to a good university, graduate, get a good job, buy a home, start a family. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: modern life :[ ")]),_c('p',[_vm._v(" The university’s position is cemented as an essential aspect of the betterment of one's future. This power is enough reason to fear the institution. If not fear, then respect it. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: and respect out of fear. ")]),_c('p',[_vm._v(" For me, my timid relationship with the University and its spaces continued till I came across The Undercommons by Stefano Harvey and Fred Moten. The shock of reading their words about the University still resonates and continues to be a means of navigation for me through spaces of higher education. In the book they state that the only possible relationship one can have with the university, the American university in particular, is to use it to one's ends and aspirations. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: This is so true and relatable. I had to stay close to my undergraduate university this summer, and whenever I could, I tried to access student-only services by showing them the front of my expired student card. ")]),_c('p',[_vm._v(" In this endeavor the university should not and cannot be mistaken as a space of truth and enlightenment. This truth founded and maintained by the university is the basis of professionalization. The university is the training camp for the state and it is the facility in which professionals are pumped out to continue the state’s mission for growth and global empowerment. The State is fraught with its ambitions of generating a worldview in support of its ambitions of unchecked growth, knowledge, and power. Although the university "),_c('i',[_vm._v("imagizes")]),_c('sup',[_vm._v("25")]),_vm._v(" itself as humanitarian in its ambitions, there is no denying its complicity and support of the States globalizing and totalitarian ambitions. ")]),_c('p',[_vm._v(" The question then is what do we do for those of us that find ourselves uneasy in our relationship with the University — those unwilling to continue down the lane of professionalization. Perhaps those spaces which we fear, those spaces which we hesitate to enter, are the spaces our presence needs to venture. Full advantage should be taken of the hospitality which the University provides to maintain its humanitarian image. Moving along those shelves are spaces for refuge, not fear. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: This is interesting - to sustain an image constitutes a complex system of providing and maintaining services - an image is not just for PR and media exposure. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: Yes, the image requires work from the university. Thank you for pointing this out. very true. ")]),_c('p',{staticClass:"head"},[_vm._v(" The Harvard Library Image - Mingjia Chen ")]),_c('p',[_vm._v(" One persistent and widespread myth has been haunting well-off middle-class Chinese parents for the past decade. The (quite frankly, commercial-toned) myth briefly goes as follows: ")]),_c('p',{staticClass:"quote"},[_vm._v(" “Aren’t you so curious about the secret to the success of Harvard students? Don’t you dream that your kid would join their ranks? The answer is simple - just look at all the students staying in the Harvard library reading rooms at 4 a.m, working diligently year-round! Persistence is key - sleep less, read more. Send your kid to the institution of their dreams the Harvard library way!” ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: this led to my thinking for my reflection in terms of fear and respect of the institution ")]),_c('p',[_vm._v(" The viral myth, circulating mainly through online chat groups such as WeChat and QQ messenger, has only been debunked recently. It had spawned a constellation of motivational materials including books and speeches and had become a specter that incessantly haunted my generation growing up. Think about the tiger mom - the stereotypical East Asian strictness on their kids’ educational careers."),_c('sup',[_vm._v("26")]),_vm._v(" One feature of the “4 a.m Harvard Library myth” is of particular interest to me. The rumor circulates mainly through the constant reposting of an image “of the Harvard students working hard in their library, their desks all lit up.” The image was eventually proved to be a generic image sourced online. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: the generic image . theres something i find interesting about this ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Or an unofficial image born out of a myth? ")]),_c('p',[_vm._v(" The above image is itself quite debunkable if one is familiar with the built environment of the Harvard campus, or any other academic libraries (and is privileged to know such information). One would not expect such density at 4 in the morning. One could also tell from the spatial arrangement of the workstations and the objects they house that the space represented is highly unlikely to be a Harvard reading room. However, it matters less that such an image is not taken from Harvard. It aligns with how Harvard has been seen and perceived outside its campus - an elite institution, the sacred site where the purist pursuit of knowledge is bundled with personal success, and the popular destination in an otherwise quite bland New England Town. The fabricated image of the “4 a.m Harvard Library” - hand in hand with (no matter how unsolicitedly) Harvard’s institutional image has fuelled and shaped at the same time the global bourgeois desire for reputation masqueraded as a commitment to personal enrichment. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: lol ")]),_c('p',[_vm._v(" It was fun to speak with someone from the other side - and we learned that many Harvard undergraduate students were intimidated by the Widener Library because they constantly encounter visitors who’d fetishize their existence as “godly,” while they were struggling to find an uninterrupted space to work, slack or just hang out."),_c('sup',[_vm._v("27")]),_vm._v(" Slightly ironically, the Widener Library was historically intended for graduate students, and the undergraduates - glorified as they are - did not have a library space dedicated to them until the establishment of the Lamont."),_c('sup',[_vm._v("28")]),_vm._v(" Harvard’s institutional image which was predicated on colonial knowledge production of the European academy has morphed partially into real and felt pressure on the majority of its community - the students. In addition to asking whether the presence of the institutional library is different from its official image, we could subsequently ask: Does the image of the institution have its own presence? ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: mmm. this flip is interesting. my instinct is yes, it does. a presence sustained through myth (like the image above) and \"authentic\" systems like school rankings, alumni lists etc. interested in what you think ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: I would also think that it does, but we can keep it an open question. I think the image has its weight, and realness in a way. It can have its positive effects in that one could be inspired by it - it partly excludes people from the elite institution, partly fuels peoples' fascination with it. ")]),_c('p',[_vm._v(" Such an image certainly has its presence for me - as soon as I arrived in Cambridge and registered for everything, I immediately showed up at the Smith Center. I wanted to get my library access card (as a student from a “partner” institution) as soon as possible. The library card, which looks slightly different from other HUIDs, grants you more access than you’d expected. In fact, one of the first open secrets and tips that I heard from students is to get a Harvard library card, so that one could cruise down the otherwise chaotic Massachusetts Ave in the comfort and exclusivity of the Harvard H2 shuttle. Simply use your thumb to cover the “library” notion on your card when you show it to the bus driver. For a moment Harvard’s exclusive image dissolves into a solution before it precipitates you out again when you are confronted with a magnetic card reader."),_c('sup',[_vm._v("29")])]),_c('p',{staticClass:"tb"},[_vm._v(" TB: never mind the previous question haha you answer it right here ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Oh right, I did..... and I just replied back to you above ")]),_c('p',[_vm._v(" P.S. The German Marxist philosopher, Ernst Bloch, wrote his magnum opus, The Principle of Hope, while he was using the facilities at the Widener Library while taking refuge from the Nazi regime. Thus far this has been the strongest image in my mind whenever visiting and using the reading room at the Widener library. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: as stated in my write up: the \"only relationship to the university is a criminal one\" take advantage of its hospitality ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Yes - it would be important to elaborate on this hospitality as we move along in the next parts. I would love to hear more from you on this. ")]),_c('div',{staticClass:"footnote"},[_c('hr'),_c('p',[_vm._v(" 24 "),_c('a',{attrs:{"target":"_blank","href":"https://www.washingtonpost.com/food/2022/02/08/mcdonalds-menu-hacks-review/"}},[_vm._v("link")])]),_c('p',[_vm._v(" 25 Imagizes: how it displays itself to the public which contradicts reality. ")]),_c('p',[_vm._v(" 26 But also it would be interesting to think for a moment about the ostensible classless image of the tiger mom: To engage with and have access to such motivational materials and to desire the institution is to assume a certain bourgeois class position - the tiger mom is the pedagogy of the Chinese middle class. ")]),_c('p',[_vm._v(" 27 From an anonymous source. ")]),_c('p',[_vm._v(" 28 Ibid. ")]),_c('p',[_vm._v(" 29 I am speaking from the perspective of a student from neighboring institution, which had already made Harvard’s image less intimidating - but nonetheless the pressure and nervousness remains. ")])]),_c('h3',[_vm._v("Reflection 4: Image and Presence")]),_c('img',{staticClass:"center",attrs:{"src":"/images/minor_presence_4.png"}}),_c('p',{staticClass:"head"},[_vm._v(" Some Definitions - Tejumola Bayowa ")]),_c('p',{staticClass:"head"},[_vm._v(" Institutional Image ")]),_c('p',[_vm._v(" ● A representation of the external and internal form of an institution's beliefs and practices as a body of staff, faculty and students presented to the general public in a positive light that enhances the institution's reputation as a viable means of producing intellectually and culturally prepared adults. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: I think in order to sustain this image the institution has to set up a complex system of operations - almost covering everything that an institution does. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: Very true. ")]),_c('p',{staticClass:"head"},[_vm._v(" Institutional presence ")]),_c('p',[_vm._v(" ● The state of affairs at an institution that exists but is suppressed by an institution due to its conflicting nature with an institution's public image. ")]),_c('p',{staticClass:"head"},[_vm._v(" Axioms for considering Image and Presence in our Research - Mingjia Chen ")]),_c('p',{staticClass:"bullet"},[_vm._v(" Axiom 1: The official image of the institution does not concern the present. It always anticipates the future (through strategic planning rather than tactical reaction"),_c('sup',[_vm._v("30")]),_vm._v(") and is always predicated on its accumulated past. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: i think it does concern itself with the present: anticipate and work within the present. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Good point! ")]),_c('p',{staticClass:"bullet"},[_vm._v(" Axiom 2: The presence of the institution felt within the community serves to focus and change with every present moment - it produces historical traces of events and is influenced by its own history, but it is only partially recorded and scattered across different forms of the archives. ")]),_c('p',{staticClass:"bullet"},[_vm._v(" Axiom 3: The institution curates its image through various means and has partial control over it. Its presence for the people it serves (and denies to serve), on the other hand, cannot be produced deliberately by itself. Its presence can only be felt through its operations by both people operating and using it. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: maybe more than partial. since the institution is in cahoots with the state, the state helps maintain its image through media ")]),_c('p',{staticClass:"bullet"},[_vm._v(" Axiom 4: The presence of an institution can travel beyond the community it situates. During the transmittance process, it gradually flattens itself into an image. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: interesting, i would like to hear more about this ")]),_c('p',{staticClass:"bullet"},[_vm._v(" Axiom 5: The institution’s image and its presence are not always in opposition; their relationship is reciprocal, and they often override each other. The goal is to show their complicity as well as antagonism. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: well said ")]),_c('p',{staticClass:"bullet"},[_vm._v(" Axiom 6: Neither the image nor the presence of an institution necessarily carries a moral value, and their structural relationship is complex and multi-layered. ")]),_c('p',{staticClass:"bullet"},[_c('u',[_vm._v("A note on the library as an architectural space:")]),_vm._v(" Although so far we concern the library only through its structural position within the community it serves, it is important to note that the architectural qualities of a particular library are important for studying its presence - as the architectural design of the library shapes its intended and unintended uses. For example, the sense of privacy of the stacks has historically provided a cruising space."),_c('sup',[_vm._v("31")]),_vm._v(" Another example is the dividers at the Lamont library, which have been creatively adopted by students during COVID to create a pod for taking their masks off."),_c('sup',[_vm._v("32")])]),_c('p',{staticClass:"tb"},[_vm._v(" TB: this will come in Part 2! ")]),_c('p',[_vm._v(" Please add more along the way! ")]),_c('div',{staticClass:"footnote"},[_c('hr'),_c('p',[_vm._v(" 30 De Certeau, The Practice of Everyday Life, xix. ")]),_c('p',[_vm._v(" 31 From an anonymous source. ")]),_c('p',[_vm._v(" 32 From an anonymous source. ")])]),_c('h3',[_vm._v("Reflection 5: "),_c('i',[_vm._v("The Popular and the Remix")])]),_c('img',{staticClass:"center",attrs:{"src":"/images/minor_presence_5.png"}}),_c('p',{staticClass:"head"},[_vm._v(" Remix - Tejumola Bayowa ")]),_c('p',[_vm._v(" When dealing with institutional image and presence a minor shift in key is required. This shift is what we are calling remix. To remix an institutional image in musical terms it is to decenter a/piece/stem by reordering the notes to create a new harmony."),_c('sup',[_vm._v("33")])]),_c('p',{staticClass:"mc"},[_vm._v(" MC: So beautifully put. ")]),_c('p',[_vm._v(" In simpler terms, it is a tool by which the presence of the institution becomes more recognizable. The term remix is used because it does not require a “new” mode of hearing — but it does require a different mode of listening."),_c('sup',[_vm._v("34")]),_vm._v(" It deals with what is existing and making something new from it out of a change in arrangement; the same way in which shuffling an album rather than listening to it in order creates new meanings and connections simply through a change in arrangement. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: I noticed that on Spotify you could \"hide\" a particular track in an album and not include it in the order at all ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: wow. we don't get this on apple music. ")]),_c('p',[_vm._v(" To remix is to open ourselves to new meanings and possibilities. It is to look, listen, and act in a way that goes beyond the obvious and the clear. Remixing is a process of inquiry and curiosity. It is queer in this sense because It asks what could come from mixing this with that; what could come from exploring the diversity that exists and bringing them together in harmonic ways. ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: Being open-minded to indeterminate orderings of the existing ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: Well said ")]),_c('p',[_vm._v(" Through remix, refusal comes out of listening beyond the totalitarian image of the popular by giving a voice to the subaltern and enlarging the pool of what is considered valuable knowledge. As Tina Campt states while speaking on her book, Listening to Images, “We must instead ‘listen’ to the larger story and attend to the quiet frequencies that surround and infuse these images with meaning.”"),_c('sup',[_vm._v("35")])]),_c('p',[_vm._v(" At the end of the day, the message is the same. Whether queer, minor, or remix, these different theoretical approaches and ways of living call for a rejection of simplistic conclusions and argue for plurality; an embrace of the complexity that shapes our lives. ")]),_c('p',[_vm._v(" Remix as a practice and method - Mingjia Chen ")]),_c('p',[_vm._v(" I have never been an expert in music and DJing. (no matter how much I dreamt about it) ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: me too. i tried. failed at it ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: I bought a synthesizer and a launch pad, still need to learn how to use them ")]),_c('p',[_vm._v(" Despite my lack of musical knowledge, when talking and thinking about remixing as a practice, a few images pop into my mind. For one, the DJ, spinning their records, skillfully distorting and augmenting the tempo and voices in their hands, smoothly fading in and out the flows of similar or different musical styles. The other image I immediately connect with is the digital producer, chopping up multiple songs on their MacBook only to stitch them into one through editing and overlaying: the skillful re-structuring of the acoustics. I also think about artistic practices that are less auditory than haptic - people who would reinterpret the cultural meaning of an object by rearranging its parts under a different structure."),_c('sup',[_vm._v("36")])]),_c('p',{staticClass:"tb"},[_vm._v(" TB: less or more auditory? ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: less? ")]),_c('p',[_vm._v(" Drifting off from the latter image of mechanical rearrangement, I wonder: Are Transformers the masters of remixing? It is fascinating to imagine the complex fictional metamorphosis that an earthly piece of machine has to undergo in order to transform itself into a silicone-based visitor from outer space."),_c('sup',[_vm._v("37")]),_vm._v(" Driven by the masculine desire to sell a miniature automobile and a robot at once, the toy designers had to reimagine and reinterpret every part of a vehicle as an organ of the cybertronian. The car doors have become their wing, and the gearbox their cannon. Catered to the politics of ideological opposition yet serving the young, Transformers is the skillful remixing of our fascination with and desire for the pure object of speed."),_c('sup',[_vm._v("38")])]),_c('p',{staticClass:"tb"},[_vm._v(" TB: well said!! ")]),_c('p',[_vm._v(" Understood from the image provoked above (very different contexts and situations), remixing as a practice involves the reinterpretation of a particular media, often popular and internalized as “the way to be,” through only rearranging its existing parts under a different, often minor structure. Whether it’s musical production, artistic practice, or the monopoly on children’s toys, the goal of the remix is to produce a different presence as a whole of the same parts. Presences that defy the normative and major understanding of their respective narratives."),_c('sup',[_vm._v("39")])]),_c('p',{staticClass:"tb"},[_vm._v(" TB: well said ^^ ")]),_c('p',[_vm._v(" Seemingly coming from an oblique angle, I think the logic of the remix helps us as a framework for understanding the materials at hand. ")]),_c('p',[_vm._v(" Remixing as a research method, then, could become queer in the sense that it allows us to enter the archival space from an oblique angle."),_c('sup',[_vm._v("40")]),_vm._v(" In our case, the mediums are the various archives that we are engaging / will engage with - the Harvard University archives, the Crimson archives, and in-person conversations. Yet, by examining these pieces of information, we are neither hoping to illustrate a continuous narrative nor produce new knowledge. After all, the information sources we engage with are not unofficial - they do not have the authority to serve as Harvard’s official image, yet they are involved in the construction of such an image. They are sometimes excluded from it - stories left untold; information left unexamined due to its sources’ lack of institutional power. The information we engage with is already entangled in Harvard’s institutional structure of the image-building machine. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: 📐 ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: 📏 ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: this is a good paragraph. i like it ")]),_c('p',[_vm._v(" Therefore, appropriating the institutional archive as our space, our De Certeauian tactic is to remix what is in it - to reinterpret the plural forms of knowledge about the institutional library into multiple discontinuous and non-linear histories about its presence within the Harvard community. A topsy-turvy trip to the Harvard yard. ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: we should look into the undercommons by moten/harvey. they speak on this a lot ")]),_c('p',{staticClass:"mc"},[_vm._v(" MC: I'll start to read it! ")]),_c('p',{staticClass:"tb"},[_vm._v(" TB: another really good paragraph ")]),_c('p'),_c('div',{staticClass:"footnote"},[_c('hr'),_c('p',[_vm._v(" 33 A combination of the musical word “piece” (a musical composition) and “episteme” (intellectually certain knowledge) ")]),_c('p',[_vm._v(" 34 To hear is different from listening. Listening requires empathetic and intellectual work on the part of the receiver. Hearing often does not. ")]),_c('p',[_vm._v(" 35 "),_c('a',{attrs:{"target":"_blank","href":"https://barnard.edu/news/listening-images"}},[_vm._v("link")])]),_c('p',[_vm._v(" 36 I want to share a slightly creepy but fun example. A Japanese YouTuber who combines parts of the Thomas toy trains with other mechanical devices to produce uncanny miniature robots. Check it out: https://www.youtube.com/watch?v=yfkgIxu8jX0 It is important to distinguish this image of the remix with narratives of survivalism and improvisation. For a more canonical example, reference Duchamp. ")]),_c('p',[_vm._v(" 37 Although, most of the time the physical forms of the good ones (autobots) reassembles that of the humanoid - after all, it is a cartoon designed to sell toys while stashing cold-war patriotism to the kids. ")]),_c('p',[_vm._v(" 38 Baudrillard, America, 26. ")]),_c('p',[_vm._v(" 39 Yapp, Minor China, 11. ")]),_c('p',[_vm._v(" 40 Ahmed, Orientations, 560 - 564. To be queer is to see the space from an oblique angle. ")])]),_c('h3',[_vm._v("Closing Thoughts: Taking a bathroom break")]),_c('p',[_vm._v(" For us, there is no conclusion at the moment. Instead, this is a resting spot to dwell on our reflections. Inspired by Tuck and Yang’s productive refusal of research as personal validation, we take a break from it. ")]),_c('p',[_vm._v(" Meanwhile, we continue to reflect on the discrepancy between how the institution preserves its official “image” and how its patrons represent its “presence” (often unacknowledged and silenced by the institution). For instance, the image of the Lamont Library is different from its presence, as testified by various accounts from its associates. ")]),_c('p',[_vm._v(" Looking back, we came into this research with an assumption that constitutes a founding error (and a starting point): We started by generalizing the academic library as a monolithic space of knowledge where any form of social use other than accessing its collection is considered unintended. This is uninformed - the discussion on the library’s social uses (sleeping, eating, sanitary, etc) is critically important in public library discussions."),_c('sup',[_vm._v("41")]),_vm._v(" This error is important for us: without it, we were not able to actually look at it with nuance and question the “appropriate” social uses of an institutional library. ")]),_c('p',[_vm._v(" As we depart this resting spot and enter the next stretch of our trip, we welcome more errors, hesitation, questions, and conversations that will guide us towards more exits. The minor as a method, as we are inspired by Hentyle Yapp, “asks us to pause and hesitate instead of answer and challenge.”"),_c('sup',[_vm._v("42")])]),_c('div',{staticClass:"footnote"},[_c('hr'),_c('p',[_vm._v(" 41 As suggested by a warm, helpful anonymous source. ")]),_c('p',[_vm._v(" 42 Yapp, Minor China, 8. ")])]),_c('div',{staticClass:"footer"},[_c('h3',[_vm._v("Bibliography")]),_c('p',[_vm._v(" Ahmed, Sara. “Orientations.” GLQ 12, no. 4 (2006): 543–574. ")]),_c('p',[_vm._v(" Baudrillard, Jean. "),_c('i',[_vm._v("America")]),_vm._v(". London: Verso, 1988. ")]),_c('p',[_vm._v(" Campt, Tina. "),_c('i',[_vm._v("Listening to Images")]),_vm._v(". Durham: Duke University Press, 2017. ")]),_c('p',[_vm._v(" Certeau, Michel de. "),_c('i',[_vm._v("The Practice of Everyday Life")]),_vm._v(". Berkeley: University of California Press, 1984. ")]),_c('p',[_vm._v(" Freeman, Geoffrey T. "),_c('i',[_vm._v("Library as Place: Rethinking Roles, Rethinking Space")]),_vm._v(". Washington, D.C: Council on Library and Information Resources, 2005. ")]),_c('p',[_vm._v(" Freeman, Lisa. “Public Libraries Are Becoming the New Social Safety Net.” Policy Note, September 28, 2017. "),_c('a',{attrs:{"target":"_blank","href":"https://www.policynote.ca/public-libraries-are-becoming-the-new-social-safety-net/"}},[_vm._v("link")]),_vm._v(". ")]),_c('p',[_vm._v(" Harney, Stefano, and Fred Moten. "),_c('i',[_vm._v("The Undercommons: Fugitive Planning & Black Study. Wivenhoe")]),_vm._v(": Minor Compositions, 2013. ")]),_c('p',[_vm._v(" Heil, Emily. “Review | McDonald's Mashed-up Menu Hacks Might Be Better Left Unmashed.” The Washington Post. WP Company, February 9, 2022. "),_c('a',{attrs:{"target":"_blank","href":"https://www.washingtonpost.com/food/2022/02/08/mcdonalds-menu-hacks-review/"}},[_vm._v("link")]),_vm._v(". . ")]),_c('p',[_vm._v(" Marshall, Daniel, Kevin P. Murphy, and Zeb Tortorici. “Queering Archives: Historical Unravelings Introduction.” "),_c('i',[_vm._v("Radical history review")]),_vm._v(", no. 120 (2014): 1–11. ")]),_c('p',[_vm._v(" Schwager, & Ulrich, L. T. (2004). "),_c('i',[_vm._v("Yards and Gates: Gender in Harvard and Radcliffe history")]),_vm._v(". Palgrave Macmillan. ")]),_c('p',[_vm._v(" Spivak, Gayatri Chakravorty. "),_c('i',[_vm._v("An Aesthetic Education in the Era of Globalization.")]),_vm._v(" Cambridge, Mass: Harvard University Press, 2012. ")]),_c('p',[_vm._v(" Yapp, Hentyle. Minor China: "),_c('i',[_vm._v("Method, Materialisms, and the Aesthetic.")]),_vm._v(" Durham: Duke University Press, 2021. ")])])])
